export function deg2rad(deg) {
    return deg * (Math.PI / 180);
}
export function getDistanceFromLatLonInKm(pointA, pointB) {
    const R = 6371; // Radius of the earth in km
    const dLat = deg2rad(pointB.latitude - pointA.latitude); // deg2rad below
    const dLon = deg2rad(pointB.longitude - pointA.longitude);
    const a = Math.sin(dLat / 2) * Math.sin(dLat / 2)
        + Math.cos(deg2rad(pointA.latitude))
            * Math.cos(deg2rad(pointB.latitude))
            * Math.sin(dLon / 2)
            * Math.sin(dLon / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    return R * c; // Distance in km
}
