export const TABLET = /(ipad|tablet|(android(?!.*mobile))|(windows(?!.*phone)(.*touch))|kindle|playbook|silk|(puffin(?!.*(IP|AP|WP))))/;
/**
 * Return `true` if device is tablet
 *
 * @param {string} ua - User Agent string
 * @returns {boolean}
 */
export function isTablet(ua) {
    const str = ua.toLowerCase();
    return TABLET.test(str);
}
