export function toBoolean(value) {
    if (typeof value === 'boolean') {
        return value;
    }
    // eslint-disable-next-line eqeqeq
    if (value === 'true' || value == '1' || value === '') {
        return true;
    }
    // eslint-disable-next-line eqeqeq
    if (value === 'false' || value == '0') {
        return false;
    }
    return value;
}
