export function debounce(fn, wait, immediate) {
    let timeout;
    return (...args) => {
        const later = () => {
            clearTimeout(timeout);
            if (!immediate) {
                fn(...args);
            }
        };
        const now = immediate && !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (now) {
            fn(...args);
        }
    };
}
