export function escape(event, callback) {
    let isEscape;
    if ('key' in event) {
        isEscape = (event.key === 'Escape' || event.key === 'Esc');
    }
    else if ('keyCode' in event) {
        // @ts-ignore
        isEscape = (event.keyCode === 27);
    }
    if (isEscape) {
        callback();
    }
}
