export class Dimensions {
    static contain(dimensions, maxDimensions) {
        let result = { ...dimensions };
        for (const prop in dimensions) {
            if (Object.prototype.hasOwnProperty.call(dimensions, prop)) {
                result = result[prop] > maxDimensions[prop] ? this.ratio(result, prop, maxDimensions[prop]) : result;
            }
        }
        return result;
    }
    static cover(dimensions, maxDimensions) {
        let result = Dimensions.contain(dimensions, maxDimensions);
        for (const prop in dimensions) {
            if (Object.prototype.hasOwnProperty.call(dimensions, prop)) {
                result = result[prop] < maxDimensions[prop]
                    ? Dimensions.ratio(result, prop, maxDimensions[prop])
                    : result;
            }
        }
        return result;
    }
    static ratio(dimensions, prop, value) {
        const aProp = prop === 'width' ? 'height' : 'width';
        return {
            [aProp]: dimensions[prop] ? Math.round((value * dimensions[aProp]) / dimensions[prop]) : dimensions[aProp],
            [prop]: value,
        };
    }
}
