import { isIos } from '../device/is-ios';
/**
 * Get iOS version
 *
 * @param {string} ua - User Agent string
 * @returns {string | undefined}
 */
export function getIosVersion(ua) {
    if (isIos(ua)) {
        const matches = ua.match(/OS (\d+)_(\d+)_?(\d+)?/);
        if (matches) {
            const [, major = '0', minor = '0', patch = '0',] = matches;
            return [
                parseInt(major, 10),
                parseInt(minor, 10),
                parseInt(patch, 10),
            ].join('.');
        }
    }
    return undefined;
}
