export const ANDROID_VERSION = /android\s([0-9.]*)/;
/**
 * Get Android version
 *
 * @param {string} ua - User Agent string
 * @returns {string | undefined}
 */
export function getAndroidVersion(ua) {
    const matches = ua.toLowerCase().match(ANDROID_VERSION);
    if (matches) {
        return matches[1];
    }
    return undefined;
}
