import BigNumber from 'bignumber.js';
export function isGoodNumber(value, nonZeroCheck = true) {
    const valueNumber = value instanceof BigNumber ? value : BigNumber(value);
    return (valueNumber.isFinite()
        && !valueNumber.isNaN()
        && valueNumber.isPositive()
        && (nonZeroCheck ? !valueNumber.isZero() : true));
}
